import React from 'react';
import classNames from 'classnames';

import styles from './ComparisonCardGroup.module.scss';

export interface ComparisonCardGroupProps {
    className?: string;
    children: React.ReactNode;
    style?: React.CSSProperties;
}

export const ComparisonCardGroup: React.FC<ComparisonCardGroupProps> = ({
    className,
    children,
    style,
}) => {
    return (
        <div className={classNames(styles.base, className)} style={style}>
            {children}
        </div>
    );
};
